*{
    box-sizing: border-box;
  }
  
  
  body,html{
    display: flex;
    flex-direction: column;
    background-color: #F6F4F2;
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
    scroll-behavior: smooth;
  }

  /* Header CSS */
#header{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    display: flex;
    padding: 14px 80px;
    justify-content: space-between;
    align-items: center;
    align-self: stretch;
    border-bottom: .5px solid #8D8D91;
    height: 50px;
    background-color: #F6F4F2;
    z-index: 999;
  }
  .logo{
    position: relative;
    display: flex;
    align-items: center;
    gap: 5.984px;
  }
  .logo .monvvo-logo{
    width: 127.2px;
    height: 19.2px;
  }
  #nav{
    display: flex;
    align-items: center;
    gap: 24px;
    margin-left: 30px;
  }
  #nav .nav-link{
    font-family: "Roboto", sans-serif;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    color: #6C6C70;
    text-decoration: none;
  }
  #nav .nav-link-active{
    font-weight: 500;
    color: #175134;
  }
  .contact-header{
    display: flex;
    justify-content: flex-end;
    align-items: center;
    flex-grow: 1;
  }
  .contact-btn{
    font-family: 'Roboto', sans-serif;
    font-size: 16px;
    background-color: #175134;
    color: #fff;
    border-radius: 3px;
    padding: 8px 12px;
    text-decoration: none;
  }
  .contact-btn:hover{
    transition: all .3s ease-in-out;
    background-color: #20894D;
  }
  
  .mobile-btn{
    display: none;
    width: 30px;
    height: 30px;
    margin: 30px;
    cursor: pointer;
  }
  .mobile-btn.disabled{
    opacity: 0.5;
    pointer-events: none;
    cursor: not-allowed;
  }
  .mobile-menu {
    position: fixed;
    top: 0;
    left: -100%; /* Start off-screen */
    width: 80%; /* Adjust width as needed */
    height: 100%;
    background-color: #F6F4F2;
    border-right: 0.5px solid #ddd;
    z-index: 99999999;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    transition: left 0.3s ease-in-out; /* Smooth slide-in effect */
  }
  .mobile-menu.active {
    left: 0; /* Slide in */
  }
  
  .mobile-close{
    position: absolute;
    top: 20px;
    right: 20px;
    width: 18px;
    height: 18px;
    cursor: pointer;
  }
  .mobile-nav{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    width: 100%;
    margin-top: 50px;
  }
  .mobile-nav .nav-link{
    font-family: 'Roboto', sans-serif;
    font-size: 16px;
    font-weight: 500;
    width: 100%;
    color: #232525;
    padding: 5px;
    margin: 5px 10px;
    text-decoration: none;
    cursor: pointer;
  }
  .mobile-nav .mobile-contact{
    color: #20894D;
  }
  #menu-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5); /* Semi-transparent black */
    z-index: 998; /* Place it below the mobile menu but above the content */
    display: none; /* Hide it by default */
  }
#title{
    font-family: "Times New Roman", serif;
    font-size: 40px;
    font-style: normal;
    line-height: normal;
    align-self: stretch;
  }
  
  .calculator-main{
    position: relative;
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    margin: 85px auto 0 auto;
    width: 100%;
  }
  .calculation-section{
    display: flex;
    width: 90%;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 80px;
  }
  .calculator-wrap{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
    width: 100%;
    height: auto;
    margin: 0;
    padding: 0 0 60px 0;
  }
  .calculator-inputs{
    position: relative;
    display: flex;
    height: auto;
    flex-direction: column;
    align-items: flex-start;
    flex-shrink: 1;
    width: 100%;
    max-width: 400px;
  }
  
  .mortgage-inputs{
    display: grid !important;
    grid-template-columns: 1fr 1fr !important; /* Example */
    gap: 20px; /* Add uniform spacing */
  }
  
  
  /* Mortgage Input Section */
  .mortgage-inputs{
    display: grid;
    grid-template-areas: 
    "mortgage-price mortgage-price"
    "mortgage-down mortgage-down"
    "mortgage-term mortgage-term"
    "mortgage-interest mortgage-interest"
    "mortgage-extra mortgage-extra";
    width: 100%;
    padding: 0 15px;
  }
  .mortgage-inputs .home-price-box{grid-area: mortgage-price;}
  .mortgage-inputs .down-payment-box{grid-area: mortgage-down;}
  .mortgage-inputs .loan-term-box{grid-area: mortgage-term;}
  .mortgage-inputs .interest-rate-box{grid-area: mortgage-interest;}
  .mortgage-inputs .mortgage-extra-box{grid-area: mortgage-extra;}
  
  .input-box{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    align-self: stretch;
    margin-bottom: 60px;
  }
  .input-box label{
    font-family: "Roboto", sans-serif;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    color: #616164;
  }
  .input-fluid{
    display: flex;
    padding: 12px 16px;
    align-items: flex-start;
    gap: 10px;
    align-self: stretch;
    border-bottom: 1px solid #232525;
  }
  .input-content{
    display: flex;
    align-items: center;
    gap: 12px;
  }
  .input-icon{
    font-family: Inter;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    /* color: #6C6C70; */
    color: #5F5F63
  }
  .input-separator{
    width: 1px;
    height: 24px;
    background: #6C6C70;
  }
  .input-box input, .input-box select{
    font-family: "Roboto", sans-serif;
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: 140%;
    color: #232525;
    background: transparent;
    border: none;
    outline: none;
    height: 24px !important;
  }
  .input-box select{
    padding-left: 10px;
  }
  .input-box input::placeholder{
    color: #232525;
    opacity: .9;
  }
  .input-box .input-text{
    font-family: 'Roboto', sans-serif;
    font-size: 16px;
    color: #6C6C70 !important;
  }
  
  /* Get Rid of webkit spinners */
  input::-webkit-inner-spin-button, input::-webkit-outer-spin-button{
    appearance: none;
    -webkit-appearance: none;
    -moz-appearance: none;
  }
  
  /* Multiple Inputs CSS */
  .multiple-inputs{
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    align-self: stretch;
    border-bottom: 1px solid #6C6C70;
  }
  
  .input-left{
    display: flex;
    padding: 12px 16px;
    align-items: flex-start;
    gap: 10px;
    flex: 1 0 0;
  }
  .input-left .input-content{
    display: flex;
    align-items: center;
    gap: 12px;
  }
  .input-right{
    display: flex;
    padding: 12px 16px;
    align-items: flex-start;
    gap: 10px;
    align-self: stretch;
  }
  .input-right .input-content{
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex: 1 0 0;
  }
  .multiple-inputs input{
    background: transparent;
    border: none;
    outline: none;
  }
  .multiple-inputs .percentage-input{
    width: 28px;
    text-align: right;
  }
  .percent-icon{
    width: 10px;
    height: 16px;
  }
  .dollar-icon{
    width: 12px;
    height: 16px;
  }
  
  /* Info Icon Input Specific CSS */
  .input-top{
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    align-self: stretch;
  }
  .info-icon{
    width: 20px;
    height: 20px;
    cursor: pointer;
  }
  .input-bottom{
    display: flex;
    padding: 12px 16px;
    align-items: flex-start;
    gap: 10px;
    align-self: stretch;
    border-bottom: 1px solid #232525;
  }
  .input-inner{
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex: 1 0 0;
  }
  
  .input-inner select{
    border: none;
    outline: none;
    background: transparent;
    flex: 1 0 0;
  }
  /* Default Select */
  select {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    background: transparent;
  }
  
  .input-dropdown{
    width: 10.517px;
    height: 6.247px;
    transform: scale(1.2);
    fill: #6C6C70 !important;
    stroke-width: 0.2px;
    stroke: #F6F4F2;
  }
  
  .input-bottom-inner{
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex: 1 0 0;
  }
  .input-bottom-inner input{
    background: transparent;
    border: none;
    outline: none;
  }
  .input-bottom-right{
    display: flex;
    align-items: center;
    gap: 12px;
  }

  /* Warning Tooltip CSS */
  .warning-tooltip {
    position: absolute;
    top: -50px; /* Position above the input */
    left: 0;
    background-color: #f8d7da;
    color: #721c24;
    padding: 10px 15px;
    border: 1px solid #f5c6cb;
    border-radius: 5px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    white-space: normal; /* Allow wrapping */
    z-index: 100;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: auto; /* Adapt to content */
    /* max-width: 400px; */
    text-align: left; /* Align text to the left for readability */
    width: 90%;
  }
  
  /* Hidden tooltip */
  .warning-tooltip.hidden {
    display: none;
  }
  
  /* Tooltip text */
  .warning-tooltip p {
    font-family: 'Roboto', sans-serif;
    margin: 0;
    padding-right: 10px;
    white-space: normal; /* Allow text to wrap */
  }
  /* Close button */
  .warning-tooltip .close-alert {
    background: none;
    border: none;
    font-size: 1rem;
    line-height: 1;
    color: #721c24;
    cursor: pointer;
  }
  .close-alert .close-btn {
    width: 14px;
    height: 14px;
  }
