  .info-tooltips {
    position: absolute;
    width: 300px; /* Fixed width */
    /* background-color: rgb(67, 70, 75); */
    background-color: rgb(57, 60, 65);
    color: #fff;
    padding: 10px 15px;
    border-radius: 6px;
    border-bottom-right-radius: 4px;
    font-size: 14px;
    font-family: "Roboto", sans-serif;
    line-height: 20px;
    text-align: center;
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
    white-space: normal;
    opacity: 0;
    pointer-events: none;
    transform: translateY(-10px);
    transition: opacity 0.2s ease-in-out, transform 0.2s ease-in-out;
    z-index: 10;
  }
  .info-tooltips.visible {
    opacity: 1;
    pointer-events: auto;
    transform: translateY(0);
  }
/* Default to hidden */
