  .sm_widget {
  overflow: hidden;
  scrollbar-width: none; /* Firefox */
}

.sm_widget::-webkit-scrollbar {
  display: none; /* Chrome, Safari, Edge */
}

  
  
  
  
  /* Amortization Table CSS */
  #amortization-schedule {
    width: 100%; /* Full width */
    overflow-y: unset;
  }
  #amortization-schedule::-webkit-scrollbar{
    display: none !important;
  }
  
  
  /* Scrollable tbody styles */
  .scrollable-tbody {
    max-height: 100px; /* Initially for 3 rows */
    overflow-y: hidden; /* Hide scrolling initially */
    transition: max-height 0.3s ease; /* Smooth expansion */
  }
  
  /* Expanded state */
  .scrollable-tbody.expanded {
    max-height: 300px; /* Expand to show 10 rows with scroll */
    overflow-y: auto; /* Enable scrolling */
  }
  
  .amortization-table {
    width: 100%; /* Ensure the table spans the full width */
    border-collapse: collapse;
  }
  
  .amortization-table thead th {
    position: relative; /* No sticky needed since it's above the scroll */
    top: 0;
    background-color: #fff; /* Match background color */
    z-index: 1;
  }
  
  .amortization-table th, .amortization-table td {
    padding: 8px 12px;
    text-align: center;
  }
  
  
  
  
  
