  }
  .btn-icon-fluid{
    display: flex;
    padding: 10px;
    justify-content: center;
    align-items: center;
    gap: 10px;
    cursor: pointer;
  }
  .btn-icon-fluid .btn-icon{
    display: flex !important;
    width: 18px;
    height: 18px;
    flex-shrink: 0;
    stroke-width: 1.5px;
    stroke: #232525;
  }
  .btn-icon-fluid .printer-text{
    display: flex !important;
    color: #232525;
    text-align: center;
    font-family: "Roboto", sans-serif;
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: 140%;
  }
  .btn-icon-fluid:hover .printer-text{
    text-decoration: underline;
  }
  
  /* How to CSS */
  .how-section{
    display: flex;
    padding: 60px 10px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 10px;
    align-self: stretch;
    background: #175134;
    box-shadow: 2px 4px 20px 5px rgba(0, 0, 0, 0.10);
  }
  .how-inner{
    display: flex;
    width: 80%;
    flex-direction: column;
    align-items: flex-start;
    gap: 0;
  }
  .how-title{
    font-family: "Times New Roman", serif;
    font-size: 32px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    color: #F6F4F2;
    align-self: stretch;
    margin: 0 0 15px 0;
  }
  .how-list{
    align-self: stretch;
    text-indent: 15px;
  }
  .how-text{
    font-family: "Roboto", sans-serif;
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: 30px;
    color: #E4E4E4;
    align-self: stretch;
    margin-bottom: 20px;
  }
  
  /* Terms Section CSS */
  .terms-section{
    display: flex;
    width: 80%;
    flex-direction: column;
    align-items: flex-start;
    gap: 32px;
    margin: 140px auto;
  }
  .terms-title{
    font-family: "Times New Roman", serif;
    font-size: 32px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    color: #232525;
    align-self: stretch;
  }
  .terms-content {
    display: grid;
    grid-template-columns: repeat(3, auto);
    justify-content: center;
    gap: 24px;
    width: 100%;
  }
  
  .terms-item {
    position: relative;
    display: flex;
    width: 500px;
    height: 400px;
    padding: 40px 24px;
    flex-direction: column;
    align-items: center;
    gap: 24px;
    border: 1px solid #E4E4E4;
    background: #FFF;
    box-shadow: 2px 4px 20px 5px rgba(0, 0, 0, 0.10);
    overflow-y: hidden;
  }
  
  .item-inner {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 8px;
    align-self: stretch;
  }
  
  .item-title {
    display: flex;
    height: 33.04px;
    flex-direction: column;
    justify-content: center;
    align-self: stretch;
    font-family: "Times New Roman", serif;
    font-size: 32px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    color: #232525;
    margin: 0;
  }
  
  .item-text {
    font-family: "Roboto", sans-serif;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 140%;
    color: #6C6C70;
    align-self: stretch;
  }
  
  .item-corner {
    position: absolute;
    right: 0;
    bottom: -1px;
    width: 60.145px;
    height: 60.145px;
  }
  







